<template>
  <Card title="客户端">
    <i-form v-if="editConfig" :label-width="100">
      <form-item label="服务端地址">
        <i-input v-model="config.remote" style="width: 200px;"></i-input>
      </form-item>
      <form-item label="服务端端口">
        <i-input v-model.number="config.port" style="width: 200px;"></i-input>
      </form-item>
      <form-item label="网络缓存">
        <i-input v-model.number="config.readBufferSize" style="width: 200px;"></i-input>
      </form-item>
      <form-item label="立即发送">
        <i-switch v-model="config.tcpNoDelay"></i-switch>
      </form-item>
      <form-item>
        <i-button type="primary" @click="connectServer">开始连接</i-button>
      </form-item>
    </i-form>
    <i-form v-else :label-width="100">
      <form-item label="服务端设置">
        {{config.remote}} : {{config.port}}
        <i-button
          size="small"
          type="primary"
          style="margin-left: 20px;"
          @click="changeConfig"
        >更改连接设置</i-button>
        <!-- <i-button size="small" type="primary" style="margin-left: 20px;" @click="changeConfig">关闭连接</i-button> -->
      </form-item>
      <form-item label="请求数据">
        <i-input v-model="requestData" type="textarea" style="width: 300px;"></i-input>
        <i-button type="primary" size="small" style="margin-left: 20px;" @click="send">发送</i-button>
      </form-item>
      <form-item label="返回数据">
        <i-input v-model="responseData" type="textarea" style="width: 300px;"></i-input>
      </form-item>
    </i-form>
  </Card>
</template>
<script type="text/javascript">
export default {
  data() {
    return {
      editConfig: true,
      config: {
        remote: "127.0.0.1",
        port: 8000
      },
      requestData: null,
      fetchResponseInterval: {},
      responseData: null
    };
  },
  created() {
    this.loadClientConfig();
  },
  methods: {
    loadClientConfig() {
      var vm = this;
      this.$axios.post("/client/config").then(data => {
        vm.config = Object.assign({}, data);
      });
    },
    connectServer() {
      var vm = this;
      this.$axios.post("/client/reconnect", vm.config).then(() => {
        vm.editConfig = false;
        vm.fetchResponseInterval = setInterval(vm.fetchResponse, 3 * 1000);
      });
    },
    changeConfig() {
      var vm = this;
      this.$axios.post("/client/stop").then(() => {
        vm.editConfig = true;
        clearInterval(vm.fetchResponseInterval);
      });
    },
    fetchResponse() {
      var vm = this;
      this.$axios.post("/client/receive").then(data => {
        vm.responseData = data;
      });
    },
    send() {
      if (!this.requestData) {
        this.$Message.info({ content: "发送数据不能为空！" });
        return;
      }
      var vm = this;
      var data = Object.assign({}, vm.config, { data: vm.requestData });
      this.$axios.post("/client/send", data).then(data => {
        vm.responseData = data;
      });
    }
  }
};
</script>