<template>
  <Card title="服务端">
    <i-form v-if="editConfig" :label-width="100">
      <form-item label="服务器端口">
        <i-input v-model.number="config.port" style="width: 200px;"></i-input>
      </form-item>
      <form-item label="服务器缓存">
        <i-input v-model.number="config.readBufferSize" style="width: 200px;"></i-input>
      </form-item>
      <form-item label="立即发送">
        <i-switch v-model="config.tcpNoDelay"></i-switch>
      </form-item>
      <form-item>
        <i-button type="primary" @click="connectServer">开始监听</i-button>
      </form-item>
    </i-form>
    <i-form v-else :label-width="100">
      <form-item label="服务端设置">
        端口 : {{config.port}}
        <i-button size="small" type="primary" style="margin-left: 20px;" @click="changeConfig">更改设置</i-button>
        <!-- <i-button size="small" type="primary" style="margin-left: 20px;" @click="changeConfig">停止监听</i-button> -->
      </form-item>
      <form-item label="连接">
        <i-table
          :data="clientList"
          :columns="clientColumns"
          highlight-row
          @on-current-change="changeSelectedClient"
        ></i-table>
      </form-item>

      <form-item label="发送数据">
        <i-input v-model="requestData" type="textarea" style="width: 300px;"></i-input>
        <i-button type="primary" size="small" style="margin-left: 20px;" @click="send">发送</i-button>
      </form-item>
    </i-form>
  </Card>
</template>
<script type="text/javascript">
export default {
  data() {
    return {
      editConfig: true,
      config: {
        readBufferSize: 1024 * 1024,
        tcpNoDelay: true,
        port: 8000
      },
      fetchClientInterval: {},
      clientList: [],
      clientColumns: [
        { key: "sessionId", title: "客户端Id" },
        { key: "repeaterId", title: "中继器Id" },
        { key: "loraId", title: "LoraId" },
        { key: "extend", title: "扩展命令" },
        { key: "emitter", title: "发射器命令" }
      ],
      selectedClient: null,
      requestData: null
    };
  },
  created() {
    this.loadServerConfig();
  },
  methods: {
    loadServerConfig() {
      var vm = this;
      this.$axios.post("/server/config").then(data => {
        vm.config = Object.assign({}, data);
      });
    },
    connectServer() {
      var vm = this;
      this.$axios.post("/server/restart", vm.config).then(() => {
        vm.editConfig = false;
        vm.fetchClientInterval = setInterval(vm.fetchClient, 1000 * 3);
      });
    },
    changeConfig() {
      var vm = this;
      this.$axios.post("/server/stop").then(() => {
        clearInterval(vm.fetchClientInterval);
        vm.editConfig = true;
      });
    },
    fetchClient() {
      var vm = this;
      this.$axios.post("/server/listClients").then(data => {
        vm.clientList = [].concat(data);
      });
    },
    changeSelectedClient(current) {
      this.selectedClient = Object.assign({}, current);
    },
    send() {
      if (!this.requestData) {
        this.$Message.info({ content: "发送数据不能为空！" });
        return;
      }
      if (!this.selectedClient) {
        this.$Message.info({ content: "请选择连接！" });
        return;
      }
      var vm = this;
      var data = {
        sessionId: vm.selectedClient.sessionId,
        data: vm.requestData
      };
      this.$axios.post("/server/send", data).then(() => {});
    }
  }
};
</script>